.c {
  height: 100vh;
  position: relative;

  .c-bg {
    width: 30px;
    height: 100%;
    background-color: #59b256;
    position: absolute;
  }

  .c-title {
    font-size: 60px;
    width: 80%;
  }

  .c-wrapper {
    padding: 50px;
    display: flex;

    .c-left {
      flex: 1;

      .c-info {
        .info-item {
          display: flex;
          align-items: center;
          margin: 50px 0px;
          font-weight: 300;
          width: 70%;

          .info-icon {
            width: 30px;
            height: 30px;
            margin-right: 20px;
          }
        }
      }
    }

    .c-right {
      flex: 1;
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;

      .c-desc {
        font-weight: 200;
      }

      .right-form {
        margin-top: 20px;

        input {
          width: 50%;
          height: 50px;
          border: none;
          border-bottom: 1px solid black;
          margin: 10px 0px;
          font-size: 14px;
          padding-left: 10px;
        }

        textarea {
          width: 100%;
          margin: 10px 0px;
          font-size: 14px;
          padding-left: 10px;
        }

        button {
          border: none;
          padding: 15px;
          background-color: #59b256;
          color: #fff;
          font-weight: 500;
          cursor: pointer;
          margin-right: 5px;
        }
      }
    }
  }
}

@media screen and (max-width: 480px) {
  .c-wrapper {
    flex-direction: column;
    padding: 0px 50px;

    .c-title {
      font-size: 30px;
    }
    .c-info {
      .info-item {
        margin: 20px 0px;
        width: 100%;
      }
    }

    .c-right {
      .c-desc {
        display: none;
      }

      .right-form {
        margin-top: 0px;
        display: flex;
        flex-wrap: wrap;

        input {
          width: 35%;
          height: 40px;
          margin: 10px;
          margin-left: 0;
        }

        button {
          margin-top: 10px;
        }
      }
    }
  }
  .c {
    height: 100%;
    .c-bg {
      height: 100%;
    }
  }
}
