.intro {
  display: flex;
  height: 100vh;

  .left {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;

    .left-wrapper {
      padding: 50px;
      height: 50%;
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      .intro-name {
        font-size: 30px;
        font-weight: 300;
      }

      .name {
        font-size: 60px;
      }

      .title {
        height: 50px;
        overflow: hidden;
        display: flex;
        align-items: center;

        .title-h2 {
          margin-right: 5px;
          font-size: 30px;
        }
      }
      .title-wrapper {
        height: 100%;
        animation: move 10s ease-in-out infinite alternate;

        @keyframes move {
          25% {
            transform: translateY(-50px);
          }
          50% {
            transform: translateY(-100px);
          }
          75% {
            transform: translateY(-150px);
          }
          100% {
            transform: translateY(-200px);
          }
        }

        .title-item {
          height: 50px;
          font-size: 30px;
          font-weight: bold;
          color: #59b256;
          display: flex;
          align-items: center;
        }
      }
    }
    .i-scroll {
      width: 30px;
      height: 30px;
      position: absolute;
      bottom: 20px;
    }
  }

  .right {
    flex: 1;
    position: relative;

    .right-image {
      width: 100%;
      height: 100%;
      object-fit: cover;
      position: absolute;
    }

    .bg {
      clip-path: polygon(25% 0%, 100% 0%, 100% 100%, 0% 100%);

      background-color: #59b256;
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      right: 0;
    }
  }
}

@media screen and (max-width: 480px) {
  .intro {
    flex-direction: column;

    .left-wrapper {
      padding: 10px;
      align-items: center;
      justify-content: center;
      text-align: center;
      height: 100%;

      .intro-desc {
        display: none;
      }
    }
  }
}
