.t {
  width: 50px;
  height: 25px;
  border-radius: 20px;
  border: 1px solid #ccc;
  background-color: #ccc;
  position: fixed;
  top: 10px;
  right: 10px;
  z-index: 999;
  display: flex;
  align-items: center;
  justify-content: space-around;

  .t-icon {
    width: 20px;
    height: 20px;
  }

  .t-button {
    width: 25px;
    height: 25px;
    border-radius: 50%;
    background-color: #999;
    outline: none;
    border: none;
    position: absolute;
    cursor: pointer;
    transition: all 1s ease;
  }
}
