.about {
  height: 100vh;
  display: flex;
  align-items: center;
}

.a-left {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  height: 100%;

  .a-card.bg {
    position: absolute;
    top: 50px;
    left: 50px;
    background-color: #333;
    // background-color: #59b256;
  }

  .a-card {
    width: 60%;
    height: 70%;
    border-radius: 30px;
    position: absolute;
    overflow: hidden;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}

.a-right {
  flex: 1;

  .right-title {
    font-weight: 400;
  }

  .right-sub {
    margin: 20px 0px;
  }

  .right-desc {
    font-weight: 300;
  }
}

@media screen and (max-width: 480px) {
  .about {
    flex-direction: column;
    text-align: center;
    margin-top: 50px;

    .a-left {
      width: 100%;

      .a-card {
        height: 30vh;
      }
      .a-card.bg {
        display: none;
      }
    }
    .a-right {
      padding: 20px;
    }
  }
}
